import * as React from 'react';
import { IBasicButton, IToolbarButtonConfig, IconPicture } from '@ali/4ever-component';
import { PluginRoles } from '@ali/4ever-bamboo';
import { Controller } from '@ali/4ever-cangjie';
import { selectImage, } from '../../actions';
import { isButtonDisabled } from '../imageButton';
import { ImagePluginConfig } from '../../types';

export interface IImageButtonProps extends IToolbarButtonConfig {
  controller: Controller;
  locale?: ImagePluginConfig['locale'];
}

const handleClick = (controller: Controller) => {
  controller.run('onAction', selectImage())
};

export default function createImageButton(props: IImageButtonProps): IBasicButton {
  const { controller, locale, icon, title, tooltip, badgeDot, disabled } = props;

  const isDisabled = disabled || isButtonDisabled(controller);

  const buttonConfig: IBasicButton = ({
    icon: icon || <IconPicture />,
    onClick: handleClick,
    title: title || locale?.quickInsertTitle,
    tooltip: {
      title: title || locale?.quickInsertTitle,
      description: 'Markdown: ![]() ' + (locale?.['space'] || 'Space'),
      ...tooltip,
    },
    badgeDot,
    disabled: isDisabled,
    role: PluginRoles.image,
    testid: "bi-toolbar-picture"
  });

  return buttonConfig;

}
